<template>
    <div class="container">
        <h2 class="page-header"><i18n path="common.install"></i18n></h2>
        <hr class="my-2">
        <p><i18n path="common.guide.useNpm"></i18n></p>
        <pre class="brush:js">
            npm i v-suggest --save
        </pre>
        <p><i18n path="common.guide.include"></i18n></p>
        <pre class="brush:js">
            import Vue from 'vue'
            import vSuggest from 'v-suggest';
            Vue.use(vSuggest);
        </pre>


        <h2 class="page-header"><i18n path="common.deploy"></i18n></h2>
        <hr class="my-2">
        <script type="syntaxhighlighter" class="brush:html">
        <![CDATA[
            <template>
                <v-suggest :data="list" show-field="name"></v-suggest>
            </template>

            < script >
                export default {
                    data(){
                        return {
                            list: [
                                {id:1 ,name:'Chicago Bulls',desc:'芝加哥公牛'},
                                {id:2 ,name:'Cleveland Cavaliers',desc:'克里夫兰骑士'},
                                {...}
                            ]
                        };
                    }
                };
            < /script >
        ]]>
        </script>

        <h2 class="page-header"><i18n path="common.example"></i18n></h2>
        <hr class="my-2">


        <div class="row">
            <div class="col-md-6">
                <h4><i18n path="suggest.demo.singleColumn"></i18n></h4>
                <pre class="p-3 bg-light">{{JSON.stringify(value1, null, 4)}}</pre>
                <p>
                    <v-suggest :data="sample" show-field="name" v-model="value1">
                    </v-suggest>
                </p>
                <showcode>
                    <script type="syntaxhighlighter" class="brush:html;gutter: false;">
                    <![CDATA[
                    <v-suggest :data="sample"
                        show-field="name"
                        v-model="value1">
                    </v-suggest>
                    ]]>
                    </script>
                </showcode>
            </div>
            <div class="col-md-6">
                <h4><i18n path="suggest.demo.custom"></i18n></h4>
                <pre class="p-3 bg-light">{{JSON.stringify(value2, null, 4)}}</pre>
                <p>
                    <v-suggest :data="sample" :show-field="resultFormat" v-model="value2">
                    </v-suggest>
                </p>
                <showcode>
                    <script type="syntaxhighlighter" class="brush:html;gutter: false;">
                    <![CDATA[
                    <v-suggest :data="sample"
                        :show-field="resultFormat"
                        v-model="value2">
                    </v-suggest>

                    //script
                    methods: {
                        resultFormat(row){
                            return row.name+' ('+row.desc+')';
                        }
                    }
                    ]]>
                    </script>
                </showcode>
            </div>
        </div>
        <br>

        <div>
            <h4><i18n path="suggest.demo.placeholder"></i18n></h4>
            <p>
                <v-suggest :data="sample" show-field="name" placeholder="Type something..." >
                </v-suggest>
            </p>
            <showcode>
                <script type="syntaxhighlighter" class="brush:html;gutter: false;">
                    <![CDATA[
                    <v-suggest :data="sample" placeholder="Type something..."></v-suggest>
                    ]]>
                    </script>
            </showcode>
        </div>
        <br>

        <div>
            <h4><i18n path="suggest.demo.disabled"></i18n></h4>
            <p>
                <v-suggest :data="sample" show-field="name" v-model="valDisabled" :disabled="true" >
                </v-suggest>
            </p>
            <showcode>
                <script type="syntaxhighlighter" class="brush:html;gutter: false;">
                    <![CDATA[
                    <v-suggest :data="sample" :disabled="true"></v-suggest>
                    ]]>
                </script>
            </showcode>
        </div>
        <br>

        <div>
            <h4><i18n path="suggest.demo.raw"></i18n></h4>
            <pre class="p-3 bg-light">{{JSON.stringify(valCallback, null, 4)}}</pre>
            <p>
                <v-suggest :data="sample" show-field="name" @values="values">
                </v-suggest>
            </p>
            <showcode>
                <script type="syntaxhighlighter" class="brush:html;gutter: false;">
                    <![CDATA[
                    <v-suggest :data="sample" @values="values"></v-suggest>

                    //script
                    methods: {
                        values(data){
                            console.log(data);
                        }
                    }
                    ]]>
                    </script>
            </showcode>
        </div>






        <h2 class="page-header"><i18n path="common.options"></i18n></h2>
        <table class="table table-bordered table-striped option-table">
            <thead>
                <tr>
                    <th><i18n path="common.tbTitle.name"></i18n></th>
                    <th><i18n path="common.tbTitle.type"></i18n></th>
                    <th><i18n path="common.tbTitle.desc"></i18n></th>
                    <th><i18n path="common.tbTitle.required"></i18n></th>
                    <th><i18n path="common.tbTitle.default"></i18n></th>
                </tr>
            </thead>
            <tbody>
                <tr>
                    <td>data</td>
                    <td>Array</td>
                    <td><i18n path="suggest.option.data"></i18n></td>
                    <td>true</td>
                    <td></td>
                </tr>
                <tr>
                    <td>show-field</td>
                    <td>String | Function</td>
                    <td><i18n path="suggest.option.showField"></i18n></td>
                    <td>false</td>
                    <td>name</td>
                </tr>
                <tr>
                    <td>placeholder</td>
                    <td>String</td>
                    <td><i18n path="suggest.demo.placeholder"></i18n></td>
                    <td>false</td>
                    <td></td>
                </tr>
                <tr>
                    <td>disabled</td>
                    <td>Boolean</td>
                    <td><i18n path="suggest.demo.disabled"></i18n></td>
                    <td>false</td>
                    <td>false</td>
                </tr>
            </tbody>
        </table>




        <h2 class="page-header"><i18n path="common.events"></i18n></h2>
        <table class="table table-bordered table-striped">
            <thead>
            <tr>
                <th><i18n path="common.tbTitle.param"></i18n></th>
                <th><i18n path="common.tbTitle.desc"></i18n></th>
                <th><i18n path="common.tbTitle.args"></i18n></th>
            </tr>
            </thead>
            <tbody>
            <tr>
                <td>values</td>
                <td><i18n path="suggest.event.values"></i18n></td>
                <td>Object</td>
            </tr>
            </tbody>
        </table>
    </div>
</template>

<script>
    export default {
        data(){
            return {
                value1: '',
                value2: '',
                valDisabled: 'Oklahoma City Thunder',
                valCallback: null,
                sample: [
                    {id:1 ,name:'Chicago Bulls',desc:'芝加哥公牛'},
                    {id:2 ,name:'Cleveland Cavaliers',desc:'克里夫兰骑士'},
                    {id:3 ,name:'Detroit Pistons',desc:'底特律活塞'},
                    {id:4 ,name:'Indiana Pacers',desc:'印第安纳步行者'},
                    {id:5 ,name:'Milwaukee Bucks',desc:'密尔沃基雄鹿'},
                    {id:6 ,name:'Brooklyn Nets',desc:'布鲁克林篮网'},
                    {id:7 ,name:'Boston Celtics',desc:'波士顿凯尔特人'},
                    {id:8 ,name:'New York Knicks',desc:'纽约尼克斯'},
                    {id:9 ,name:'Philadelphia 76ers',desc:'费城76人'},
                    {id:10,name:'Toronto Raptors',desc:'多伦多猛龙'},
                    {id:11,name:'Atlanta Hawks',desc:'亚特兰大老鹰'},
                    {id:12,name:'Charlotte Hornets',desc:'夏洛特黄蜂'},
                    {id:13,name:'Miami Heat',desc:'迈阿密热火'},
                    {id:14,name:'Orlando Magic',desc:'奥兰多魔术'},
                    {id:15,name:'Washington Wizards',desc:'华盛顿奇才'},
                    {id:16,name:'Denver Nuggets',desc:'丹佛掘金'},
                    {id:17,name:'Minnesota Timberwolves',desc:'明尼苏达森林狼'},
                    {id:18,name:'Oklahoma City Thunder',desc:'俄克拉荷马雷霆'},
                    {id:19,name:'Portland Trail Blazers',desc:'波特兰开拓者'},
                    {id:20,name:'Utah Jazz',desc:'犹他爵士'},
                    {id:21,name:'Golden State Warriors',desc:'金州勇士'},
                    {id:22,name:'Los Angeles Clippers',desc:'洛杉矶快船'},
                    {id:23,name:'Los Angeles Lakers',desc:'洛杉矶湖人'},
                    {id:24,name:'Phoenix Suns',desc:'菲尼克斯太阳'},
                    {id:25,name:'Sacramento Kings',desc:'萨克拉门托国王'},
                    {id:26,name:'Dallas Mavericks',desc:'达拉斯小牛'},
                    {id:27,name:'Houston Rockets',desc:'休斯顿火箭'},
                    {id:28,name:'Memphis Grizzlies',desc:'孟菲斯灰熊'},
                    {id:29,name:'New Orleans Pelicans',desc:'新奥尔良鹈鹕'},
                    {id:30,name:'San Antonio Spurs',desc:'圣安东尼奥马刺'}
                ]
            };
        },
        methods: {
            resultFormat(row){
                if(row){
                    return row.name + ' (' + row.desc + ')';
                }
            },
            values(datas){
                this.valCallback = datas;
            }
        },
        mounted(){
            SyntaxHighlighter.highlight();
        }
    }
</script>